div.contain {
  width:280px;
  height:400px;
  text-align: center;
  background: #fff;
  border-radius:10px;
}

div.contain h3 {
  font-size: 30px;
  letter-spacing: 3px;
  margin-top: 15px;
  color: #000;
  font-family: 'Roboto', sans-serif;
}
  
  div.contain div.properties {
    height: 130px;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    font-family: 'Roboto', sans-serif;
  }

  div.contain div.properties div {
    display: inline-block;
    text-align: center;
    width:50%;
  }
  div.contain div.properties div h4 {
    font-size: 20px;
    padding: 25px 0px 5px;
  }
  div.contain div.properties .qty .fa {
    font-size: 7px;
    color: #757575;
    background: #dfdfdf;
    border-radius: 100%;
    padding: 4px;
    cursor: pointer;
  }
  div.contain div.properties .qty .number {
    font-size: 20px;
    padding: 5px;
  
  }
  div.contain div.properties .price .fa {
    font-size: 18px;
  }
  div.contain div.properties .price .amount {
    font-size: 20px;
    padding: 0 5px;
  }

  div.contain .ip-add-cart {
    margin-top:-8px;
    border: none;
    padding: 12px 50px;
    background: #f22d34;
    color: #fff;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;
    border-radius: 25px;
    box-shadow: 0px 15px 45px rgba(242, 45, 52, 0.5);
    cursor: pointer;
    transition: background 0.3s ease-in;
  }
  div.contain .ip-add-cart:hover {
    background: #da1920;
  }
  
